<template>
  <div class="m-products-list">
    <ul>
      <li
        v-for="item in nav"
        :key="item.key"
        :class="{ 's-nav-active': item.active }"
      >
        {{ item.name }}
      </li>
    </ul>
    <el-row>
      <Item v-for="(item, index) in productiList" :key="index" :meta="item" />
    </el-row>
  </div>
</template>
<script>
import Item from "@/components/products/item.vue";
import api from "@/api/index.js";
export default {
  components: {
    Item,
  },
  data() {
    return {
      nav: [
        { key: "s-default", name: "智能排序", active: true },
        { key: "s-price", name: "价格排序", active: false },
        { key: "s-sale", name: "人气最高", active: false },
        { key: "s-comment", name: "评价最高", active: false },
      ],
      productiList: [],
    };
  },
  created() {
    api.getProductsList().then((res) => {
      this.productiList = res.data.data;
    });
  },
};
</script>


