<template>
  <div class="page-changeCity">
    <!-- 省份选择 -->
    <el-row>
      <provinct />
    </el-row>
    <!-- 热门城市 -->
    <el-row>
      <hot title="热门城市：" :list="hotList" />
    </el-row>
    <!-- 最近访问 -->
    <el-row>
      <hot title="最近访问：" :list="recentList" />
    </el-row>
    <!-- 按首字母选择 -->
    <el-row>
      <category />
    </el-row>
  </div>
</template>
<script>
import Provinct from "@/components/changeCity/province.vue"; //省份选择
import hot from "@/components/changeCity/hot.vue"; //热门城市/最近访问
import Category from "@/components/changeCity/category.vue"; //按首字母选择
import api from "@/api/index.js";
export default {
  data() {
    return {
      hotList: [],
      recentList: [],
    };
  },
  created() {
    api.getHotCity().then((res) => {
      this.hotList = res.data.data.map((item) => item.name);
    });
    api.getRecentCity().then((res) => {
      this.recentList = res.data.data.map((item) => item.name);
    });
  },
  components: {
    Provinct,
    hot,
    Category,
  },
};
</script>


