import Vue from 'vue';
import Router from 'vue-router';
import defaultPage from '@/layout/default.vue'; //首页路由
import blankPage from '@/layout/blank.vue';     //登录页路由
import goodsList from '@/page/goodsList.vue';   //不知道这是那
import Index from '@/page/index.vue';           //首页主体路由
import ChangeCity from '@/page/changeCity.vue'; //切换城市路由
import Login from '@/page/login.vue'            //登陆路由
import Register from '@/page/register.vue'      //注册路由
Vue.use(Router)

export default new Router({
  // mode: 'history',  //history模式
  routes: [
    {
      //首页路由
      path: '/',
      name: 'defaultPage',
      component: defaultPage,
      redirect: '/index',
      children: [{
        //搜索框分类路由
        path: '/name',
        name: 'goods',
        component: goodsList
      }, {
        //首页主体路由
        path: '/index',
        name: 'index',
        component: Index
      }, {
        //切换城市路由
        path: '/changeCity',
        name: "changeCity",
        component: ChangeCity
      }]
    }, {
      path: '/blank',
      name: 'blankPage',
      component: blankPage,
      children: [{
        path: 'login',
        name: 'login',
        component: Login
      }, {
        path: 'register',
        name: 'register',
        component: Register
      }]
    }
  ]
})



