<template>
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <el-col :span="3" class="left">
        <router-link to="/">
          <img
            src="https://s0.meituan.net/bs/fe-web-meituan/10afbf1/img/logo.png"
            alt=""
          />
        </router-link>
      </el-col>
      <el-col :span="15" class="center">
        <div class="wrapper">
          <el-input
            v-model="searchWorld"
            placeholder="输入商家或地址"
            @focus="focusInput"
            @blur="blurInput"
            @input="input"
          ></el-input>
          <el-button type="primary" icon="el-icon-search">搜索</el-button>
          <!-- 热门搜索 -->
          <dl class="hotPlace" v-if="isHotPlace">
            <dt>热门搜索</dt>
            <dd v-for="(item, index) in hotPlaceList" :key="index">
              <router-link :to="{ name: 'goods', params: { name: item } }">
                {{ item }}
              </router-link>
            </dd>
          </dl>
          <!-- 搜索栏下拉框 -->
          <dl class="searchList" v-if="isSearchList">
            <dd v-for="(item, index) in searchList" :key="index">
              <router-link :to="{ name: 'goods', params: { name: item } }">
                {{ item }}
              </router-link>
            </dd>
          </dl>
        </div>
        <!-- 搜索建议 -->
        <p class="suggest">
          <a :href="item" v-for="(item, index) in suggestList" :key="index">
            {{ item }}
          </a>
        </p>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import api from "@/api/index.js";
export default {
  data() {
    return {
      searchWorld: "",
      isFocus: false,
      // 热门搜索
      hotPlaceList: [],
      // 搜索栏下拉框数据
      searchList: [],
      // 搜索建议
      suggestList: [],
    };
  },
  computed: {
    isHotPlace: function () {
      return this.isFocus && !this.searchWorld;
    },
    isSearchList: function () {
      return this.isFocus && this.searchWorld;
    },
  },
  methods: {
    focusInput() {
      this.isFocus = true;
    },
    blurInput() {
      let self = this;
      setTimeout(function () {
        self.isFocus = false;
      }, 100);
    },
    input() {
      let val = this.searchWorld;
      api.getSearchList().then((res) => {
        this.searchList = res.data.data.list.filter((item, index) => {
          return item.indexOf(val) > -1;
        });
      });
    },
  },
  created() {
    api.searchHotWords().then((res) => {
      this.hotPlaceList = res.data.data;
      this.suggestList = res.data.data;
    });
  },
};
</script>



